import { FacebookProvider, Like } from "react-facebook";

import styled from "styled-components";

const FacebookProviderDiv = styled.div`
  .facebookProviderClassname {
    background: orange;
  }
`;

function FacebookLike() {
  return (
    <FacebookProviderDiv>
      <FacebookProvider
        className="facebookProviderClassname"
        appId="379850369411744"
        className="facebookProviderClassname"
      >
        <Like
          href="https://www.facebook.com/revelermusicllc"
          colorScheme="light"
          showFaces
          share
        />
      </FacebookProvider>
    </FacebookProviderDiv>
  );
}

export default FacebookLike;
